.App {
  text-align: center;
}

header {
  background-color: #402080;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #eee;
}

body {
  margin: 0;
  background-color: black;
}

span {
  margin: 12px;
}

.Grid {
  padding: 5px;
}

button {
  background: #ddd;
  float: center;
  font-size: 24px;
  font-weight: bold;
  margin-right: -1px;
  margin-top: -1px;
  min-height: 10vh;
  border: 5px solid black;
  color: black;
  padding: 4px;
  text-align: center;
}

button:focus {
  background: #999;
  outline: none;
}

button:active {
  background: #666;
}